<script>


export default {

  name: 'CustomComponent',
  data () {
    return{
      name: '',
      backgroundColor: '#EEE',
      borderColor: '#DDD',
      authors: ['3', '2'],
		currentAuthor: '',
    page: 0,
		posts: [],
    totalPages: '',
    seen: false,
    isLoading: '',
    show: true,
    range: 0,
    ts_type: [
      {id: '', name: "All"},
      {id: 'site', name: "Text"},
      {id: 'image', name: "Image"},
      {id: 'video', name: "Video"}
    ],
    currentType: '',
    authorID: '',
    apiURL: 'http://reviewdrop.local/wp-json/wp/v2/reviews/?per_page=4',
    apiUsr: 'http://reviewdrop.local/wp-json/wp/v2/users/',
    plan: '',
    aTrustpilot: '',
    aGoogle: '',
    aTrip: '',

    }
  },
  created: function created() {
  this.getAuthor();

},
watch: {

    currentAuthor: ['getAuthor','getPosts'],
    currentType() {
  this.page = 0;
  this.totalPages = 0;
  this.posts = [];
    this.show = true;
  this.getPosts();

}

   },

  mounted(){
    let q = this.$router.currentRoute.query
    this.backgroundColor = q.backgroundColor
    this.borderColor = q.borderColor
    this.currentAuthor = q.currentAuthor

  },
  methods:{

    getPosts: function() {

			var xhr = new XMLHttpRequest();
			var self = this;
      self.page++
      self.isLoading = 'is-loading'

			xhr.open('GET', self.apiURL + '&page=' + self.page + '&author=' + self.currentAuthor + '&ts_type=' + self.currentType);
			xhr.onload = function () {
        self.totalPages = xhr.getResponseHeader('X-WP-TotalPages')

        if(self.page == self.totalPages){
    self.show = false
}
var newPosts = JSON.parse(xhr.responseText)

        newPosts.forEach(function(element) {
            self.posts.push(element)
        })
        self.isLoading = null
			};
			xhr.send();
		},
    getAuthor: function(){
      var xhr = new XMLHttpRequest();
			var self = this;


      // self.page++
      // self.isLoading = 'is-loading'


			xhr.open('GET', self.apiUsr + self.currentAuthor);
			xhr.onload = function () {
        self.totalPages = xhr.getResponseHeader('X-WP-TotalPages')

        if(self.page == self.totalPages){
    self.show = false
}
var author = JSON.parse(xhr.responseText);

        self.plan = author.acf.plan;
    
        self.aTrustpilot = author.acf.review_links.u_trustpilot;
        self.aGoogle = author.acf.review_links.u_google;
        self.aTrip = author.acf.review_links.u_tripadvisor;

			};
			xhr.send();
    }


  },
  // computed: {
  //     filteredItems() {
  //       return this.posts.filter(post => {
  //          return post.acf.ts_type == 'image'
  //       })
  //     }
  //   }

}
</script>

